:root {
  --background--color: rgb(255, 252, 235);
  --background--color-alpha: rgb(255, 252, 235, 0.75);
  --text--color: rgb(68, 68, 68);
  --link--color: rgb(97, 84, 136);
  --highlight--color: rgb(229, 111, 135);
  --upa--color: rgb(47, 144, 177);
  --mayawati--color: rgb(102, 169, 49);
  --mahagathbandhan--color: rgb(102, 169, 49);
  --nda--color: rgb(255, 187, 12);
  --others--color: rgb(162, 117, 171);
  --aap--color: rgb(107, 233, 255);
  --shadow--color: rgba(68, 68, 68, 0.2);
}

html {
  font-size: 62.5%;
}

body {
  background: rgb(255, 252, 235);
  background: var(--background--color);
  border: 0;
  color: rgb(68, 68, 68);
  color: var(--text--color);
  font-family: 'Work Sans', sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  letter-spacing: 0.05em;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

a {
  color: rgb(97, 84, 136);
  color: var(--link--color);
  padding: 0 0.2rem;
}

a:active,
a:hover {
  background-color: rgb(97, 84, 136);
  background-color: var(--link--color);
  color: rgb(255, 252, 235);
  color: var(--background--color);
  text-decoration: none;
}

p {
  margin: 2rem 0 0 0;
}

p:first-child {
  margin-top: 0;
}

.India {
    display: block;
    height: auto;
    margin: 0 auto;
    padding: 0;
    position: relative;
    width: 100%;
}

.India .component-wrapper {
    display: block;
    margin: 4rem 2rem 0 2rem;
    position: relative;
    width: auto;
}

@media screen and (min-width: 960px) {
    .India .component-wrapper {
        margin: 6rem auto;
        max-width: 1280px;
        width: 85%;
    }
}

.India .highlight {
    color: var(--highlight--color);
}

.India .cols {
    -webkit-columns: 1;
       -moz-columns: 1;
            columns: 1;
    margin-top: 4rem;
}

@media screen and (min-width: 760px) {
    .India .cols {
        -webkit-columns: 1;
           -moz-columns: 1;
                columns: 1;
        margin: 2rem auto 2rem;
        width: 75%;
    }
}

@media screen and (min-width: 960px) {
    .India .cols {
        -webkit-columns: 1;
           -moz-columns: 1;
                columns: 1;
        margin: 3rem auto 3rem;
        width: 50%;
    }
}

@media screen and (min-width: 1200px) {
